body {
  font-family: 'Lato', sans-serif;
  overflow: hidden;
}

p {
  line-height: 25px;
}

.container {
  max-width: 1530px;
  padding: 0 15px;
  margin: 0 auto;
}

.wrapper {
  background-image: url(../images//header-bg.jpeg);
  height: 100vh;
  overflow: scroll;
  padding-bottom: 60px;
}

.search__button {
  padding: 5px;
  background-color: royalblue;
  color: white;
  border: none;
  border-radius: 0px 5px 5px 0px;
  cursor: pointer;
  transition: background-color 0.3s;
  &:hover {
    background-color: rgb(48, 79, 173);
  }
}


.more__button{
  background-color: royalblue;
  padding: 5px 30px;
  border-radius: 10px;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
  display: block;
  margin: 20px auto;

  &:hover {
    background-color: rgb(48, 79, 173);
  }
}

.search__input {
  width: 700px;
  padding: 4px;
  border-radius: 5px;
  border-radius: 5px 0 0 5px;
  border: none;

  @media(max-width:920px) {
    width: 500px;
  }

  @media(max-width:643px) {
    width: 400px;
  }
}

.white {
  color: #fff;
}
.center {
  display: block;
  margin: 0 auto;
}

a{
  color: inherit;
}

.close{
  margin: 20px;
}

.mt-300{
  margin-top: 300px;
}