


.cart{
    min-height: 900px;
    // width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .cart__inner{
        display: grid;

        // grid-template-columns: repeat(4, 1fr);
        grid-template-columns: repeat(auto-fill, 367px);
        grid-gap: 10px;
        border-radius: 10px;
        margin-top: 10px;
        // margin: 0 auto;
        width: 100%;
        justify-content: center;


        @media(max-width:920px) {
            grid-template-columns: repeat(auto-fill, 300px); 
        }

        @media(max-width:639px) {
            grid-template-columns: repeat(auto-fill, 240px); 
        }

        @media(max-width:519px) {
            grid-template-columns: repeat(auto-fill, 190px); 
        }

        @media(max-width:419px) {
            grid-template-columns: repeat(auto-fill, 320px);  
        }
    }

    .full__cart-link{
        display: block;
        @media(max-width:920px) {
            width: 300px;
        }

        @media(max-width:639px) {
            width: 243px;
        }

        @media(max-width:519px) {
            width: 183px;
        }

        @media(max-width:419px) {
            width: 329px;
        }

        @media(max-width:356px) {
            text-decoration: none;
            width: 320px;
        }
    }

    .cart__item{

        width: 100%;
        height: 100%;
        display: inline-block;
        padding: 40px 40px 10px 50px;
        text-align: left;
        background-color: rgba(255,255,255, 0.6);
        border-radius: 10px;
        transition: all .3s linear;

        @media(max-width:642px) {
            padding: 40px 30px 10px 35px;


        }

        @media(max-width:639px) {
            padding: 10px;
        }


        @media(max-width:419px) {
            padding: 40px;
        }

        @media(max-width:356px) {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding: 10px;
         
        }
        &:hover{
            transform: scale(0.99);

        }
        img {
            width: 260px;
            object-fit: contain;
            border-radius: 7px;
            min-width: 230px;
            max-height: 200px;
            // width: 200px;
            // height: 200px;


            @media(max-width:519px) {
                min-width: 150px;
            }

            @media(max-width:419px) {
                height: 346px;
                // min-height: 100%;
                max-height: none !important;
            }

            @media(max-width:356px) {
                height: 300px;
            }
        }
        &:hover{
            background-color: white;
            cursor: pointer;
        }
        .cart__title{
            margin: 5px 0;

            @media(max-width:639px) {
                font-size: 15px;
            }

            @media(max-width:419px) {
                font-size: 20px;
            }

            @media(max-width:356px) {
                font-size: 15px;
            }
        }

        .cart__description{
           
            

            @media(max-width:639px) {
                font-size: 12px;
                line-height: 15px;
            }

            @media(max-width:419px) {
                font-size: 20px;
                line-height: 23px;
            }

            @media(max-width:356px) {
                font-size: 15px;
                line-height: 18px;
            }
        }
    }

    .cart__content{
        max-width: 260px;
    }
}